<template>
  <div>
    <loading :active.sync="isLoading"></loading>
    <frontBkgTop :cartData="cartList" @afterRemove="gerCart"></frontBkgTop>
    <div class="container  pt-2">
      <div class="teacher1 bg-secondary my-5 p-5">
