              alt
            />
          </div>
          <div class="col-10">
            <h2></h2>
            <p></p>
            <h2></h2>

            <ul class="list-group list-group-flush ">
                <li class="list-group-item bg-secondary"><a href="https://www.gq.com.tw/blog/neganchor/detail-5159.html"></a></li>
                <li class="list-group-item bg-secondary"><a href="https://www.gq.com.tw/blog/neganchor/detail-4414.html"></a></li>
                <li class="list-group-item bg-secondary"><a href="https://www.gq.com.tw/blog/neganchor/detail-4134.html"></a></li>
            </ul>
          </div>
        </div>
        <div>
            
        </div>
      </div>
       <div class="teacher2 bg-secondary my-5 p-5">
        <div class="row">
          <div class="col-2">
            <img
              class="rounded-pill"
              src="https://img.gq.com.tw/userfiles/sm/sm160160_images_VBL/34/2015110985631481.png"
              alt
            />
          </div>
          <div class="col-10">
            <h2>Wright)</h2>
            <p> </p>
            <h2></h2>

            <ul class="list-group list-group-flush ">
                <li class="list-group-item bg-secondary"><a href="https://www.gq.com.tw/blog/wright/detail-4293.html"></a></li>
                <li class="list-group-item bg-secondary"><a href="https://www.gq.com.tw/blog/wright/detail-4083.html"></a></li>
                <li class="list-group-item bg-secondary"><a href="https://www.gq.com.tw/blog/wright/detail-3715.html"></a></li>
            </ul>
          </div>
        </div>
        <div>
            
        </div>
      </div>
      <div class="teacher3 bg-secondary my-5 p-5">
        <div class="row">
          <div class="col-2">
