                <li class="list-group-item bg-secondary"><a href="https://www.gq.com.tw/blog/emily/detail-3147.html"></a></li>
                <li class="list-group-item bg-secondary"><a href="https://www.gq.com.tw/blog/emily/detail-2876.html">?</a></li>
            </ul>
          </div>
        </div>
        <div>
            
        </div>
      </div>
    </div>
    <frontBkgBtn></frontBkgBtn>
  </div>
</template>


<script>
import frontBkgTop from "@/components/frontEnd/frontBkgTop";
import frontBkgBtn from "@/components/frontEnd/frontBkgBtn";
import Alert from "@/components/both/AlertMessage";

export default {
  data() {
    return {
      cartList: [],
      isLoading: false
    };
  },
  methods: {
    gerCart() {
      const api = `${process.env.APIPATH}/api/${process.env.COUSTOMPATH}/cart`;
      const vm = this;
      vm.isLoading = true;
      this.$http.get(api).then(response => {
        vm.cartList = response.data.data.carts;
        vm.isLoading = false;
      });
    }
  },
  created() {
    this.gerCart();
  },
  components: {
    frontBkgTop,
    frontBkgBtn,
    Alert
  }
};
</script>


